<template>
	<div>
		<p>https://www.cnblogs.com/miracle77hp/articles/11163532.html
		</p>
		<p>git remote add origin url ///本地仓库与远程仓库关联
		</p>
		<p>git clone [url] ///拉取项目
		</p>
		<p>
		</p>
		<p>git init ///当前目录创建git代码库
		</p>
		<p>git add 路径/指定文件 ///暂存区
		</p>
		<p>git commit -m "注释" ///暂存区转移到仓库
		</p>
		<p>git remote add origin + 远程仓库地址 //链接远程仓库，创建主分支
		</p>
		<p>git pull origin master // 把本地仓库的变化连接到远程仓库主分支
		</p>
		<p>git push -u origin master ///推送
		</p>
		<p>
		</p>
		<p>git status ///查看变更文件
		</p>
		<p>git config --list ///配置文件
		</p>
		<p>git config --global user.name "用户名"///设置提交代码时的用户信息
		</p>
		<p>git config --global user.email "邮箱"///设置提交代码时的邮箱
		</p>
		<p>
		</p>
		<p>
		</p>
		<p>git clone [ssh url]///拉取ssh
		</p>
		<p>ssh-keygen -t rsa ///创建公钥</p>

		<el-button @click="readTestFile">test</el-button>
	</div>
</template>

<script>
	export default {
		methods: {
			readTestFile() {
				const file = this.loadFile('./src/file/test.txt')
				console.log(this.unicodeToUtf8(file))
			},
			// 读取文件
			loadFile(name) {
				const xhr = new XMLHttpRequest()
				const okStatus = document.location.protocol === 'file:' ? 0 : 200
				xhr.open('GET', name, false)
				xhr.overrideMimeType('text/html;charset=utf-8') // 默认为utf-8
				xhr.send(null)
				return xhr.status === okStatus ? xhr.responseText : null
			},
			// unicode转utf-8
			unicodeToUtf8(data) {
				data = data.replace(/\\/g, '%')
				return unescape(data)
			}
		}
	}
</script>

<style>
</style>



