<template>
	<el-row :gutter="20" style="background-color:#87c3ff;">
		<el-col :span="20" :offset="3">
			<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect"
			 background-color="#87c3ff" text-color="#000000" active-text-color="#0080ff">
				<el-menu-item index="main_blog">首页</el-menu-item>
				<el-menu-item index="git">博客</el-menu-item>
				<el-submenu index="study" disabled>
					<template slot="title">导航B</template>
					<el-menu-item index="2-1">选项1</el-menu-item>
					<el-menu-item index="2-2">选项2</el-menu-item>
					<el-menu-item index="2-3">选项3</el-menu-item>
					<el-submenu index="2-4">
						<template slot="title">选项4</template>
						<el-menu-item index="2-4-1">选项1</el-menu-item>
						<el-menu-item index="2-4-2">选项2</el-menu-item>
						<el-menu-item index="2-4-3">选项3</el-menu-item>
					</el-submenu>
				</el-submenu>
			</el-menu>
		</el-col>
	</el-row>
</template>

<script>
	export default {
		data() {
			return {
				activeIndex: '1'
			};
		},
		methods: {
			handleSelect(key) {
				this.$store.commit('changeNavpage', key)
			},
		}
	}
</script>

<style>
</style>



