<template>
  <div>
    <component v-bind:is="privateNavpage"></component>
  </div>
</template>
<script>
  import main_blog from '../blog/main_blog.vue'
  import git from '../blog/git.vue'
  export default {
    components: {
      main_blog,
	  git
    },
    data() {
      return {
        privatebloglist: this.$store.state.bloglist,
        privateNavpage: 'main_blog'
      }
    },
    methods: {
      handleSelect(key) {
        this.$store.commit('changeNavpage', key)
		console.log(key)
      },
    },
    computed: {
      changeNavpage() {
        return this.$store.state.Navpage;
      }
    },
    watch: {
      changeNavpage() {
        this.privateNavpage = this.$store.state.Navpage;
      }
    }
  }

</script>
<style>
</style>



