<template>
  <div id="app">
    <Nav></Nav>
    <el-row :gutter="20">
      <el-container style="width: 100%;padding-top: 50px;padding-bottom: 50px;">
        <el-col :span="15" :offset="3">
          <el-main v-bind:style="{minHeight: Height+'px'}" style="border: solid 1px gray;border-radius: 10px;">
            <main_left></main_left>
          </el-main>
        </el-col>
        <el-col :span="4">
          <main_right></main_right>
        </el-col>
      </el-container>
      <footer></footer>
      <el-footer style="text-align: center;">Copyright © 2018 Anwesende Powered by Vue</el-footer>
    </el-row>
  </div>
</template>

<script>
  import header_left from './components/system/header_left.vue'
  import header_right from './components/system/header_right.vue'
  import main_left from './components/system/main_left.vue'
  import main_right from './components/system/main_right.vue'
  import Nav from './components/system/nav.vue'
  export default {
    name: 'App',
    components: {
      header_left,
      header_right,
      main_left,
      main_right,
      Nav
    },
    data() {
      return {
        Height: 0,
        Width: 0
      }
    },
    methods: {},

    mounted() {
      //动态设置内容高度 让footer始终居底   header+footer的高度是100
      this.Height = document.documentElement.clientHeight * 0.8;
      //监听浏览器窗口变化　
      window.onresize = () => {
        this.Height = document.documentElement.clientHeight * 0.8;
        this.autoRootFontSize()
      }
    },
  }

</script>

<style>
  #app {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
  }

  div {
    overflow: hidden;
  }

</style>



