import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	strict: false,
	state: {
		Navpage: '',
		bloglist:['github','csharpstring','test','elementstydu']
	},
	mutations: {
		changeNavpage: function(state, param) {
			state.Navpage = param
		}
	},
	actions: {
		changeNavpage: function(ctx, param) {
			ctx.commit('changeNavpage', param)
		}
	}
})



